<template>
    <div class="myDetailsMv">
         <van-sticky>

             <!-- 顶部导航栏 -->
            <van-nav-bar
                title="MV"
            >
            <template #left>
                    <span class="iconfont icon-arrow-fine-up-copy" @click.stop="onClickLeft"></span>
            </template>
            </van-nav-bar>

            <!-- 视频区 -->
            <video class="myVideos"  autoplay controls loop ref="myVideos" :src="mvObj.url"></video>

         </van-sticky>
        
        

        <!-- 视频详情与评论 -->
        <van-tabs v-model="active" sticky>

            <van-tab  v-for="(item,index) in dataTile" :title="index ==1 ? item+'('+mvAute.commentCount+')' : item" :key="index">
                 <div v-if="active==0" class="artist">
                          <!-- 第一行 -->
                          <div class="artistOne">
                              <img v-lazy="mvAute.cover">
                              <p class="oneText">
                                  <span>{{mvAute.artistName}}</span>
                                  <span>{{mvAute.subCount}}关注</span>
                              </p>
                              <span class="icon-jiahao iconfont">关注</span>
                          </div>
                          <!-- 第二行 -->
                          <div class="artistTwo">
                                     {{mvAute.desc}}
                          </div>
                          <!-- 第三行 -->
                          <div class="artistThree">
                              <span>{{mvAute.playCount}}播放</span>
                              <span>{{mvAute.publishTime}}</span>
                          </div>
                           <!-- 第四行 -->
                          <div class="artistFour">{{mvAute.name}}</div>
                          <!-- 功能区 -->
                          <div class="artisFive">
                              <span @click="zan1" class="iconfont icon-zan1">{{mvAute.shareCount}}</span>
                              <span @click="ff_collect" class="iconfont icon-shoucang2">收藏</span>
                              <span @click="showShare = true" class="iconfont icon-zhuanqu-04">分享</span>
                          </div>

                          <!-- 相关Mv -->
                          <div class="simil">
                                  <div class="similHeard">相关Mv</div>
                              <!-- 相关mv推荐 -->
                                  <div class="similItem" v-for="(item,index) in mvList" :key="index" @click="genItem(item.id)">
                                      <div class="itemHeard">
                                          <img v-lazy="item.cover">
                                          <p class="itemP">
                                              <span class="icon-bofangjian1 iconfont"></span>
                                              
                                              <span>{{item.duration  | ff_time}}</span>
                                              <span>{{item.playCount | ff_sum}}</span>
                                          </p>
                                      </div>
                                      <div class="itemBodys">
                                          <p>
                                              <span>{{item.name}}</span>
                                              <span>-{{item.artistName}}</span>
                                          </p>
                                          <p>来自:{{item.alg}}专辑</p>
                                      </div>
                                  </div>
                          </div>
                 </div>
                 <div v-if="active==1" class="pinglun" >
                          <div class="pinglunHeard">全部评论<span>{{mvAute.commentCount}}</span></div>
                          <div class="pinglunItem" v-for="(item,index) in punList" :key="index">
                              <img v-lazy="item.user.avatarUrl">
                              <div class="ItemBodys">
                                  <p>
                                      <span>{{item.user.nickname}}</span>
                                      <span class="iconfont icon-zan" @click="ff_zan(item)">{{item.likedCount}}</span>
                                  </p>
                                  <p>{{item.time | ff_getdate}}</p>
                                  <p>
                                      {{item.content}}
                                  </p>
                              </div>
                          </div>
                 </div>
            </van-tab>
        </van-tabs>


        <!-- 分享面板 -->
        <van-share-sheet
        v-model="showShare"
        title="立即分享给好友"
        :options="options"
        @select="onSelect"
        />
    </div>
</template>

<script>
    import '../assets/less/DetailsMv.less'
    import Video from './Video.vue';
    export default {
  components: { Video },
        name : 'DetailsMv',
        data() {
            return {
                ids      : null,
                mvObj    : {},
                dataTile : ['详情','评论'],
                active   : 0,
                mvAute   : {},
                mvList   : [],
                punList  : [],
                showShare: false,
                options: [
                    { name: '微信', icon: 'wechat' },
                    { name: '微博', icon: 'weibo' },
                    { name: '复制链接', icon: 'link' },
                    { name: '分享海报', icon: 'poster' },
                    { name: '二维码', icon: 'qrcode' },
                ],

            }
        },
        created(){
             // 关闭音乐播放器
             this.ff_isFlash();
            
             this.ids = this.$route.query.id;
             
             // 获取mv详情
             this.ff_url();

             // 获取mv详情
             this.ff_detail();

             // 获取类似mv
             this.ff_leisMv();

           
     
        },
        methods: {
            // 获取mv详情
            ff_detail(){
                this.axios({
                       url : '/mv/detail',
                       params : {
                           mvid : this.ids
                       }
                })
                .then(res=>{
                    if(res.data.code == 200){
                        // Object.assign(this.mvAute,res.data.data);
                        this.mvAute = res.data.data
                        console.log(this.mvAute)
                    }
                    // console.log(res)
                })
                .catch(err=>{
                    console.log(err)
                })

            },
            // 获取mv地址
            ff_url(){
                   this.axios({
                        url : '/mv/url',
                        params : {
                            id : this.ids
                        }
                    })
                    .then(res=>{
                        
                        if(res.data.code == 200){

                            this.mvObj =  res.data.data;

                        }
                    
                    })
                    .catch(err=>{
                        console.log(err)
                    })
              
            },
            // 获取类似mv
            ff_leisMv(){
                this.axios({
                    url : '/simi/mv',
                    params : {
                        mvid : this.ids
                    }
                })
                .then(res=>{
                    if(res.data.code == 200){
                        this.mvList = []
                         res.data.mvs.map(item=>{
                             this.mvList.push(item)
                         });
                         console.log(this.mvList)
                    }
                    
                })
                .catch(err=>{
                    console.log(err)
                })
            },
            // 更换视频
            genItem(id){
                    console.log
                     this.ids = id;
                    
                    // 获取mv详情
                    this.ff_url();

                    // 获取mv详情
                    this.ff_detail();

                    // 获取类似mv
                    this.ff_leisMv();
            },
            // 获取mv的评论 
            ff_pinlun(){
                this.axios({
                    url : '/comment/mv',
                    params : {
                        id : this.ids
                    }
                })
                .then(res=>{
                    if(res.data.code == 200){
                        this.punList = [];
                        res.data.comments.map(Item=>{
                            this.punList.push(Item)
                        })
                        console.log('冲',this.punList)
                        this.$toast.clear();
                    }
                })
                .catch(err=>{
                    console.log(err)
                })
            },
            // 点赞评论
            ff_zan(item){
               
                let useId = localStorage.getItem('useId');
                if(useId){

                    item.likedCount++

                }else{
                    this.$router.push({name:'Login'})
                    this.$toast('请登录')
                }
            },
            // 点赞mv
            zan1(){
                let useId = localStorage.getItem('useId');
                if(useId){

                    this.mvAute.shareCount++

                }else{
                    this.$router.push({name:'Login'})
                    this.$toast('请登录')
                }
            },
            // 收藏
            ff_collect(){
                let useId = localStorage.getItem('useId');
                if(useId){
                     this.axios({
                        url    : '/mv/sub',
                        params : {
                            mvid     : this.ids,
                            t        : 1
                        }
                    })
                    .then(res=>{
                        console.log(res)
                    })
                    .catch(err=>{
                        console.log(err)
                    })

                }else{
                    this.$router.push({name:'Login'})
                    this.$toast('请登录')
                }
               
            },
            // 分享面板
            onSelect(option) {
                this.$toast(option.name);
                this.showShare = false;
            },
            //  再返回上一级
            onClickLeft(){
                 this.$router.go(-1);
            },
            // 关闭播放器
            ff_isFlash(){ 
                // 暂停音乐
                if(this.music.myAudio){
                    this.ff_isPlay(false);
                    this.music.myAudio.pause();
                    this.$store.commit('gaiIsflash',false);

                }
                
    
            },
            // 修改按钮状态
            ff_isPlay(data){
                this.$store.dispatch('gaiPaly',data)
            },
        
        },
        watch:{
            mvObj(news,olds){
                  if(news){
                    
                  }
            },
            active(news,olds){
                  if(news == 1){
                    this.$toast('加载中...')
                    // 获取mv的评论
                    this.ff_pinlun();
                  }
            }
        },
        filters:{
           
                // 时间处理
                ff_time(time){
                        let clock = (time-0)/1000;
                        //分钟
                        let minute = clock / 60;
                        let minutes = parseInt(minute);
                        if (minutes < 10) {
                            minutes = "0" + minutes;
                        }

                        //秒
                        let second = clock % 60; 
                        let seconds = Math.floor(second);
                        if (seconds < 10) {
                            seconds = "0" + seconds;
                        }
                        let clockStr = `${minutes}:${seconds}`
                        return clockStr;
                },
                // 化万处理
                ff_sum(sum){
                   let sumStr = '';
                   sumStr = `${Math.floor((sum-0)/10000)}万`;
                   return sumStr;
                },
                // 时间戳转化
                ff_getdate(times) {
                    var now = new Date(times),
                        y = now.getFullYear(),
                        m = now.getMonth() + 1,
                        d = now.getDate();
                    return y + "-" + (m < 10 ? "0" + m : m) + "-" + (d < 10 ? "0" + d : d) + " " + now.toTimeString().substr(0, 8);
                }
                
            
        },
        computed:{
            music(){
                return this.$store.state.song; 
            }
        }
    }
</script>

<style lang="less" scoped>

</style>