<template>
    <div class="myVideo">
        <van-pull-refresh
            v-model="isLoading"
            success-text="刷新成功"
            @refresh="onRefresh"
            >
               <!-- 内容 -->
               <div class="videoList">
               
                    <div class="box" v-for="(item,index) in nowMvData" :key="index" @click="goPack(item.id)">
                        <van-skeleton :round="true" avatar-shape="square"  title row-width=48%   :loading="loading">
                            <div class="bodys">
                                <img class="img_01" v-lazy="item.cover"/>
                                <div class="moBox"></div>
                                <span class="yan">MV</span>
                                <img class="img_02" v-lazy="item.cover"/>
                            </div>
                        </van-skeleton>
                        <van-skeleton :round="true" avatar-shape="square"  title  :row="4" :loading="loading">
                            <div class="bottomText">
                                <p>
                                    <span>{{item.name}}</span>
                                    <span>-{{item.artistName}}</span>
                                </p>
                                <p>
                                    <span class="icon-sanjiaoxing iconfont">99.5万</span>
                                    <span class="icon-zan iconfont">99.5万</span>
                                </p>
                            </div>
                        </van-skeleton>
                    </div>
               
             
         </div>
        </van-pull-refresh>


        
      
    </div>
</template>

<script>
    import '../assets/less/Video.less'
    export default {
        name : 'Video',
        data() {
            return {
                 newMvData : [],
                 nowMvData : [],
                 isLoading : false,
                 stats     : 0,
                 end       : 6,
                 loading   : true,

            }
        },
        created(){
            this.$toast.loading({
                message: '加载中',
                forbidClick: true,
                loadingType: 'spinner',
            });
            this.axios({
                url    : '/mv/first',
            })
            .then(res=>{
                if(res.data.code == 200){
                     this.$toast.clear();
                     this.newMvData = res.data.data;
                     this.nowMvData = this.newMvData.slice(this.stats,this.end);
                       console.log(this.nowMvData)
                     setTimeout(()=>{
                                this.loading = false
                     },500);
                }
               
            })
            .catch(err=>{
                console.log(err)
            })
        },
         methods: {
            //  下拉刷新
            onRefresh() {
               this.loading = true 
            setTimeout(() => {
                this.end   = this.end >= 30 ?  0 : this.end;
                this.stats = this.end ;
                this.end   = this.end + 6;
                
                this.nowMvData = this.newMvData.slice(this.stats,this.end);
              
                this.$toast('刷新成功');
                this.isLoading = false;
                this.loading   = false 
            }, 1000);
            },
            // 跳转路由
            goPack(id){
                this.$router.push({name:"DetailsMv",query :{id}});
            }
        },
    }
</script>

<style lang="less" scoped>

</style>